//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $family: null) {
	@if $family != null {
		font-family: $family; }
	@else {
		font-family: $lato; }
	font-style: $style;
	font-weight: $weight; }

//LATO
//regular
@font-face {
	@include fontdefault(normal, 400);
	@include fontpath('Lato Regular', 'Lato-Regular', lato-regular); }
@font-face {
	@include fontdefault(italic, 400);
	@include fontpath('Lato Italic', 'Lato-Italic', lato-italic); }

//MONTSERRAT
// extralight
@font-face {
	@include fontdefault(normal, 200, $montserrat);
	@include fontpath('Montserrat Extralight', 'Montserrat-Extralight', montserrat-extralight); }
@font-face {
	@include fontdefault(italic, 200, $montserrat);
	@include fontpath('Montserrat Extralight Italic', 'Montserrat-Extralightitalic', montserrat-extralightitalic); }
//regular
@font-face {
	@include fontdefault(normal, 400, $montserrat);
	@include fontpath('Montserrat Regular', 'Montserrat-Regular', montserrat-regular); }
@font-face {
	@include fontdefault(italic, 400, $montserrat);
	@include fontpath('Montserrat Regular Italic', 'Montserrat-RegularItalic', montserrat-regularitalic); }
//bold
@font-face {
	@include fontdefault(normal, 700, $montserrat);
	@include fontpath('Montserrat Bold', 'Montserrat-Bold', montserrat-bold); }
@font-face {
	@include fontdefault(italic, 700, $montserrat);
	@include fontpath('Montserrat Bold Italic', 'Montserrat-BoldItalic', montserrat-bolditalic); }

//ARBUTUS SLAB
//regular
@font-face {
	@include fontdefault(normal, 400, 'Arbutus Slab');
	@include fontpath('Arbutus Slab Regular', 'Arbutusslab-Regular', arbutusslab-regular); }
