}

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding-top: 47px;
}

@media only screen and (max-width: 767px) {
  .header {
    padding-top: 20px;
  }
}

.header__nav {
  position: relative;
  z-index: 1000;
  float: right;
  margin-right: -7px;
  width: 31px;
  height: 31px;
  background: none;
}

.header__nav.is-active:before {
  transform: translate3d(0px, -3px, 0) rotate(-45deg) scaleX(1.4);
}

.header__nav.is-active:after {
  transform: translate3d(0px, 2px, 0) rotate(45deg) scaleX(1.4);
}

.header__nav:before, .header__nav:after {
  content: '';
  position: absolute;
  right: 7px;
  width: 17px;
  height: 3px;
  background: #3F566E;
  transform-origin: right center;
  transition: transform 0.25s;
}

.header__nav:before {
  top: 8px;
}

.header__nav:after {
  bottom: 8px;
}

.logo {
  position: relative;
  z-index: 1000;
  display: inline-block;
  vertical-align: top;
  font-size: 31px;
}

.logo__icon {
  display: block;
  fill: #3F566E;
}

.nav {
