.contacts {
	max-width: 460px;
	&__title {
		margin-bottom: 20px;
		color: $yellow;
		font-family: $arbutus;
		@include f(69px, 400, 76px);
		text-align: center;
		@include lg {
			@include f(50px, null, 1.2); }
		@include md {
			font-size: 40px; } }
	&__fields {
		@include clr; }
	&__field {
		float: right;
		width: 100%;
		max-width: 452px;
		&:not(:last-child) {
			margin-bottom: 35px; } }
	&__footer {
		margin-top: 24px;
		@include clr; }
	&__submit {
		float: right; } }
