.slider {
	@include sm {
		padding-bottom: 40px; }
	&__pictures {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		@include md {
			left: 50%; }
		@include sm {
			position: relative;
			left: 0;
			margin-top: 71px;
			height: 300px; } }
	&__picture {
		position: absolute;
		z-index: 2;
		opacity: 0;
		pointer-events: none;
		transition: opacity 0.8s;
		@include md {
			right: auto;
			left: 0; }
		@include sm {
			position: absolute;
			top: 50%;
			left: 50%;
			right: auto;
			max-width: 100%;
			max-height: 100%;
			transform: translate3d(-50%,-50%,0); }
		&.is-active {
			opacity: 1; }
		&_1 {
			top: 115px;
			right: -375px; }
		&_2 {
			top: 140px;
			right: -402px; }
		&_3 {
			top: 75px;
			right: -207px; } }
	&__slide {
		padding-top: 75px;
		height: 706px;
		background: $white;
		@include vertical;
		text-align: left;
		@include sm {
			padding-top: 20px;
			height: auto;
			text-align: center; }
		&_yellow {
			.slider__title,
			.slider__title-sm {
				color: $yellow; } }
		&_pink {
			.slider__title,
			.slider__title-sm {
				color: $pink; } }
		&_azure {
			.slider__title,
			.slider__title-sm {
				color: $azure; } } }
	&__info {
		display: inline-block;
		vertical-align: middle;
		width: 100%;
		max-width: 590px;
		@include lg {
			max-width: 500px; }
		@include md {
			width: 50%; }
		@include sm {
			width: 100%;
			max-width: 300px; } }
	&__label {
		margin-bottom: 3px;
		font-family: $montserrat;
		@include f(18px, 700, 26px);
		letter-spacing: 1.51px;
		@include sm {
			@include f(14px, null, 1.3); } }
	&__subtitle {
		margin-bottom: 3px;
		font-family: $montserrat;
		@include f(53px, 700, 35px);
		letter-spacing: 1.51px;
		@include lg {
			@include f(46px, null, 1); }
		@include sm {
			font-size: 24px; } }
	&__title {
		font-family: $montserrat;
		@include f(74px, 700, 60px);
		letter-spacing: 2.115px;
		@include lg {
			@include f(60px, null, 1); }
		@include sm {
			font-size: 36px; } }
	&__title-sm {
		font-family: $montserrat;
		@include f(68px, 700, 56px);
		letter-spacing: 1.94px;
		@include lg {
			@include f(50px, null, 1); }
		@include md {
			font-size: 46px; }
		@include sm {
			font-size: 30px; } } }
